.header {
  margin-bottom: 110px;


  &__title {
    font-family: Inter, sans-serif;
    font-size: 40px;
    font-weight: 700;
    line-height: 52px;
    text-transform: uppercase;

    margin: 0;
    margin-bottom: 20px;

    @include on-tablet {
      grid-column: 1/5;

      font-size: 48px;
      line-height: 62px;

      margin-bottom: 30px;


    }

    @include on-desktop {
      font-size: 62px;
      line-height: 78px;
      font-size: 62px;

    }
  }

  &__text {
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;

    margin: 0;

    @include on-tablet {
      grid-column: 1/4;

    }
  }

  &__menu {
    display: none;


    @include on-tablet {
      display: flex;
      gap: 30px;
      align-items: center;

      &--text {
        font-family: 'Inter', sans-serif;
        font-size: 14px;
        font-weight: 300;
        line-height: 100%;

        color: #4F4F4F;
      }
    }


    &--top {
      display: flex;
      align-items: center;
    }
  }

  &__photo {
    margin-bottom: 40px;


    @include on-tablet {
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: -10;

    margin-bottom: 0;

    }

    &--position {
      width: 100%;
    }
  }

  &__contant {


    @include on-tablet {
      position: relative;

      @include page-grid;
    }
  }

  &__pic {
    display: block;
  }

  &__language {
    width: 50px;
    height: 18px;
    display: flex;
    background-image: url(../images/icons/language_UA.png);


    &:hover {
      background-image: url(../images/icons/language_EA.png);
    }
  }
}
