.page {
  scroll-behavior: smooth;
  // margin: 0;


  &__body {
    // min-width: 320px;
    margin: 0;
  }

  &__menu {
    pointer-events: none;

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);

    opacity: 0;

    transition: all 0.3s;

    &:target {
      pointer-events: all;
      z-index: 10;
      transform: translate(0);
      opacity: 1;
    }
  }
}
